<template lang="jade">
  section.ow
    // header
    owHeader
    
    // pages
    // keep-alive
    // transition(name="fade")
    //  router-view.scroll-content

    swiper.scroll-content(:options="swiperOption" ref="mySwiper")
      swiper-slide
        Home
      swiper-slide
        FAQ
      swiper-slide
        NEWS
      swiper-slide
        PC
      swiper-slide
        Phone
      swiper-slide
        EventList

</template>

<script>
import base from 'components/base'
import owHeader from 'components/ow/owHeader'
import owFooter from 'components/ow/owFooter'
import Home from './Home'
import FAQ from './FAQ'
import NEWS from './NEWS'
import PC from './PC'
import Phone from './Phone'
import EventList from './EventList'
export default {
  name: 'Ow',
  mixins: [base],
  data () {
    return {
      hasHeader: false,
      hasFooter: false,
      swiperOption: {
        // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
        // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
        notNextTick: true,
        // swiper configs swiperapi
        // autoplay: 3000,
        // direction: 'horizonal',
        grabCursor: true,
        // setWrapperSize: true,
        // autoHeight: true,
        // pagination: '.swiper-pagination',
        // paginationClickable: true,
        // prevButton: '.swiper-button-prev',
        // nextButton: '.swiper-button-next',
        // scrollbar: '.swiper-scrollbar',
        // mousewheelControl: true,
        observeParents: true,
        // if you need use plugins in the swiper, you can config in here like this
        // debugger
        debugger: true,
        // swiper callbacks
        // swiperswiper
        onTransitionStart (swiper) {
          console.log(swiper)
        }
        // more Swiper configs and callbacks...
        // ...
      },
      pages: {
        faq: 1,
        news: 2,
        pc: 3,
        phone: 4,
        eventlist: 5
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  watch: {
    // 
    '$route': 'openRoute'
  },
  mounted () {
    this.swiper.slideTo(this.$route.query.url ? this.pages[this.$route.query.url] : 0)
  },
  methods: {
    openRoute ({path, query: {url}}) {
      console.log(url)
      this.swiper.slideTo(url ? this.pages[url] : 0, 300, false)
    }
  },
  components: {
    owHeader,
    owFooter,
    Home,
    FAQ,
    NEWS,
    PC,
    Phone,
    EventList
  }
}
</script>

<style lang="stylus">

</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus">
  @import '../../var.stylus'
  section.ow
    background #fff
</style>






