<template lang="jade">
  .scroll-content.activity-center
    .tabs
      .button 
      .button 
      .button 
    router-link.banner.section(v-for=" (a, i) in activities " v-bind:to=" sources[a.activityType - 1].href ")
      img(:src=" sources[a.activityType - 1].src ")
      h4.text-black {{ a.title }}
      h5 {{ a.subTitle }}

    .empty(v-if=" activities.length === 0 ") 

    
</template>

<script>
// v-bind:class=" [ 'ds-icon-activity-' + (i + 1) ] "
// :src=" '/assets/activity/banner' + (i + 1) + '.jpg' "
  import api from '../../http/api'
  export default {
    data () {
      return {
        sources: [
          {href: '/activity/5-2-1', src: '/static/activity/banner1.jpg', title: '', describe: ''},
          {href: '/activity/5-2-2', src: '/static/activity/banner2.jpg', title: '', describe: '58'},
          {href: '/activity/5-2-3', src: '/static/activity/banner3.jpg', title: '', describe: ''},
          {href: '/activity/5-2-4', src: '/static/activity/banner4.jpg', title: '', describe: ''},
          {href: '/activity/5-2-5', src: '/static/activity/banner5.jpg', title: '', describe: '18'},
          {href: '/activity/5-2-6', src: '/static/activity/banner4.jpg', title: '', describe: ''}
        ],
        activities: [
        ]
      }
    },
    mounted () {
      this.getAllActivityList()
    },
    methods: {
      getAllActivityList () {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.getAllActivityList).then(({data}) => {
          // success
          if (data.success === 1) {
            this.activities = data.activityList || []
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else this.$message.error('')
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .tabs
    color #fff
    .button
      display inline-block
      width 1rem
      border-bottom 2px solid rgba(0,0,0,0)
      background-color rgba(0,0,0,0)
      opacity .5
      .on
        border-color #fff
        opacity 1
      &:hover
        border-color #fff
        opacity .8
        background-color rgba(0,0,0,0)
        
      
  .activity-center
    padding PWX
    padding-top 1rem
    bg-gradient(45deg, #319afa, #4ff1ef)
    
    .section
      position relative
      display inline-block
      text-align left
      // width 4.15rem
      width 80%
      // margin PW
      padding PW
      
      box-sizing border-box
      // padding PW
      // border 1px solid #d4d4d4
      // padding-top 1.5rem
      radius()
      cursor pointer
      background-size 100%
      &:hover
        background-color #fff
      
      h4,h5
        margin .08rem .05rem
        margin-bottom 0
      h4
        font-size .14rem
        font-weight bold
      h5 
        font-size .12rem
      img
        width 45%
  
</style>



